import React, {useEffect, useState} from "react";
import { useHistory, useParams } from "react-router-dom";

import styled from "styled-components";
import './Detail.scss';

let Box = styled.div`
    padding : 20px;
`;

let Ttl = styled.h4`
    font-size : 25px;
    color : ${props => props.color}
`;

function Detail(props) {
    let [alert, setAlert] = useState(true);
    // 보일 때, 업데이트 될 때 등등 자주 실행됨
    useEffect(() => {
        // console.log("111111");
        // setTimeout(() => {
        //     console.log("2초 지남");
        //     console.log(document.getElementById("alert2"));
        //     document.getElementById("alert2").style.visibility = "hidden";
        //     // document.getElementById("alert2").style.display = "none";
        // }, 2000)
        //
        // return function onUndock() {
        //     console.log("사라질 때");
        // }
    });
    // 여러 개 쌉가능
    // 순서대로 실행됨
    useEffect(() => {
        setTimeout(() => {
            setAlert(false);
        }, 2000)
    }, [alert])
    useEffect(() => {

    })
    useEffect(() => {

    })

    let history = useHistory();
    let { id } = useParams();
    let data;
    let stock = props.stock[id];

    for (const d of props.data) {
        if(d.id.toString() === id.toString()) {
            data = d
            break;
        }
    }

    return (
        <div className="container">
            <Box>
                <Ttl className="red">sdsdfsdffsdf</Ttl>
            </Box>
            {
                (alert === true)
                    ? <div id="alert2" className="my-alert2">
                        <p>재고가 얼마 남지 않았습니다.</p>
                    </div>
                    : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src={data.src} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{data.title}</h4>
                    <p>{data.content}</p>
                    <p>{data.price}</p>
                    <Stock stock={stock}/>
                    <button className="btn btn-danger" onClick={() => {
                        const temp = [...props.stock];
                        temp[id] -= 1;
                        props.setStock(temp);
                    }}>주문하기</button>
                    <button className="btn btn-dark" onClick={() => { history.goBack() }}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Stock(props) {
    return(
        <p>{ '재고 : ' + props.stock}</p>
    )
}

